@import "_variables";

.header {
  @include setup(
    $backgroundColor: map-get($colors, "castro"),
    $backgroundImage: "/images/tile_purple.gif",
  );
}

.main {
  @include setup(
    $backgroundColor: map-get($colors, "castro"),
    $backgroundImage: "/images/tile_purple.gif",
    $linkColor: map-get($colors, "red"),
    $linkVisitedColor: map-get($colors, "flamenco"),
    $linkActiveColor: map-get($colors, "electricViolet2"),
  );
}
